.download {
  display: flex;
  justify-content: center;
  align-items: center;

  padding: 40px 40px 40px 100px;
  &__search {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    max-width: 100vh;
    width: 100%;

    padding-left: 30px;
  }
  &__input-wrap {
    display: flex;
    flex-direction: column;
    width: 100%;

  }
  &__input-text {
    .basic-text;
    color: @color-grey;
  }
  &__input {
    .basic-text;
    font-weight: 500;
    padding: 18px;
    color: #2c3d59;
    border: none;
    outline: none;
    box-shadow: 0.1px 3px 7px 1px rgba(34, 60, 80, 0.2);
  }
  &__button {
    .basic-text;
    line-height: 1.2;
    width: 160px;
    height: 57px;
    border-radius: 50px;
    border: none;
    outline: none;

    color: @color-white;
    background-color: @color-grey;
    box-shadow: 0.1px 3px 7px 1px rgba(34, 60, 80, 0.2);
    cursor: pointer;

    margin-right: 26px;
    transition: opacity 0.2s linear;
    &:hover,
    &:active,
    &:focus {
      opacity: 0.9;
      outline: none;
    }
  }
}

@media (max-width: 960px) {
  .download {
    padding: 20px 20px 30px 70px;
    &__search {
      flex-direction: column-reverse;
      justify-content: flex-start;
      align-items: flex-start;
      gap: 20px;
    }
    &__input-text {
      font-size: 14px;
    }
    &__button {
      width: 120px;
      height: 40px;
      font-size: 14px;
    }
  }
}
